
import axios from 'axios';
import $ from 'jquery'
const proxyUrl = "https://cors-anywhere.herokuapp.com/"



export async function fetchCountryId() {
    const url = `${proxyUrl}https://www.gunpolicy.org/index.php?option=com_api&app=gpodatapage&clientid=306&key=b7bb356715bf99d6d04e75d266d689db&resource=getlocations&format=raw`;
    const request = new Request(url);
    return fetch(request).then(result => {
        if (result.ok) {
            return result.json();
        }
        throw result;
    }).then(result => {
        const finalResult = result.locations;
        let mapResult = new Map();
        finalResult.map(x => mapResult.set(x.id, x.name));
        return (mapResult);
    })
        .catch((error) => console.log(error))
}

export async function fetchPrivatelyOwnedFireArms(countryIdList, category) {
    let listOfRanking = new Map();
    let promises = []
    await countryIdList.forEach(async countryId => {

        const url = `${proxyUrl}https://www.gunpolicy.org/index.php?option=com_api&app=gpodatapage&clientid=306&key=b7bb356715bf99d6d04e75d266d689db&resource=getcategorydata&category=${category}&location_id=${parseInt(countryId)}&format=raw`;
        let response = fetch(url)
            .then((response => {
                if(response.ok){
                    return response.json();
                }
                throw response;
            }))
            .then(result => {
                let finalResult = cleanUpEndpointDataForLineChart(result.result);
                listOfRanking.set(countryId, finalResult)
            })
            .catch((error) => console.log(error))
        promises.push(response);
    });
    return Promise.all(promises).then(() => { return listOfRanking });
}

function cleanUpEndpointDataForLineChart(data) {
    // remove annotations
    let result = data.columnValue.replace(/\{([^}]+)\}/g, "");
    let arrayResult = result.split(';');
    let finalResultMap = new Map();
    arrayResult.forEach(element => {
        if (element === undefined || element === '')
            return;
        let splitElement = element.split(':')
        splitElement = splitElement.map(value => {
            if (value !== '' && value !== undefined) {
                if (value[0] === ' ') {
                    value = value.substring(1);
                }
                value = value.replace(/,/g, "")
                return value;
            }
        })

        finalResultMap.set(splitElement[0], Number(splitElement[1]))
    });
    return finalResultMap;
}